/*
-------------------------------------------------------
1. Default Styles
-------------------------------------------------------
*/
.btn {
  color: #222;
  padding: 0.5em 1em;
  text-align: center;
  line-height: 1.5em;
  border-width: 2px;
  border-style: solid;
  border-radius: 0.3rem;
  transition: background 0.3s, border 0.3s, color 0.3s;
  background-color: #cecece;
  border-color: #cecece;
  display: inline-block;
  vertical-align: middle; }
  .btn:focus, .btn:hover {
    outline: none;
    text-decoration: none;
    background-color: #b5b5b5;
    border-color: #b5b5b5; }
  .btn span {
    display: inline-block;
    vertical-align: middle; }
  .btn-icon {
    fill: #222;
    display: inline-block;
    vertical-align: middle;
    width: 1em;
    height: 1em;
    transition: fill 0.3s; }
    span + .btn-icon,
    .btn-icon + span {
      margin-left: 0.7em; }
  .btn--outline:not(:hover):not(:focus) {
    color: inherit;
    background-color: transparent; }
    .btn--outline:not(:hover):not(:focus) .btn-icon {
      fill: #222; }
  .btn:disabled, .btn.is-disabled {
    z-index: -1;
    opacity: 0.6;
    position: relative;
    pointer-events: none; }

/*
-------------------------------------------------------
2. Button Variations
-------------------------------------------------------
*/
.btn--main {
  color: #fff;
  background-color: #24292e;
  border-color: #24292e; }
  .btn--main .btn-icon {
    fill: #fff; }
  .btn--main:focus, .btn--main:hover {
    background-color: #3a434b;
    border-color: #3a434b; }

.btn--mainOutline {
  color: #24292e;
  background-color: transparent;
  border-color: #24292e; }
  .btn--mainOutline .btn-icon {
    fill: #24292e; }
  .btn--mainOutline:focus, .btn--mainOutline:hover {
    color: #fff;
    background-color: #3a434b;
    border-color: #3a434b; }
    .btn--mainOutline:focus .btn-icon, .btn--mainOutline:hover .btn-icon {
      fill: #fff; }

/*
=======================================================
Form Elements
=======================================================
1. Base Styles
2. Custom Checkbox/Radio
3. Custom Select
4. Disabled Elements
=======================================================
*/
/*
-------------------------------------------------------
1. Base Styles
-------------------------------------------------------
*/
.input,
.select,
.input-icon {
  appearance: none;
  background-color: #fff;
  border: solid 1px #cecece;
  border-radius: 0.3rem;
  padding: 0.65em;
  transition: border 0.3s, box-shadow 0.3s;
  display: inline-block;
  vertical-align: middle; }

.input:not(textarea),
.select:not([multiple]) {
  height: 2.75em; }

.input:focus,
.select:focus,
:focus + .input-icon {
  border-color: #b5b5b5;
  box-shadow: 0 0 1px #b5b5b5;
  outline: none; }

:-ms-input-placeholder {
  color: #b5b5b5; }

::-webkit-input-placeholder {
  color: #b5b5b5; }

/*
-------------------------------------------------------
2. Custom Checkbox/Radio
-------------------------------------------------------
*/
.inputRadio input,
.inputCheckbox input {
  margin: -1px;
  overflow: hidden;
  width: 1px;
  height: 1px;
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.inputRadio.is-disabled,
.inputCheckbox.is-disabled {
  cursor: default; }

.input-icon {
  margin-top: -0.1em;
  width: 1.5em;
  height: 1.5em;
  background-position: center;
  background-repeat: no-repeat; }
  .inputRadio .input-icon {
    border-radius: 50%; }
  .inputCheckbox :checked + .input-icon {
    background-image: url("../../dist/svg/icon-check.svg");
    background-size: 60%; }
  .inputRadio :checked + .input-icon {
    z-index: -1;
    position: relative; }
    .inputRadio :checked + .input-icon::after {
      content: '';
      border-radius: 50%;
      border: 0.45em solid #fff;
      background-color: #222;
      display: inline-block;
      vertical-align: middle;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; }

/*
-------------------------------------------------------
4. Custom Select
-------------------------------------------------------
*/
.select:not([multiple]) {
  background-image: url("../../dist/svg/icon-arrow-alt.svg");
  background-position: right 0.6em center;
  background-repeat: no-repeat;
  background-size: 0.85em 1em;
  padding-right: 2.4em; }

/*
-------------------------------------------------------
3. Disabled Elements
-------------------------------------------------------
*/
:disabled:not(.btn),
.input[readonly] {
  color: #b5b5b5;
  background-color: #e8e8e8; }

:disabled {
  pointer-events: none; }
  :disabled + .input-icon {
    background-color: #e8e8e8; }
    :disabled + .input-icon + span {
      color: #b5b5b5; }

